.App {
  margin-top: 20vh;
  text-align: center;
}
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
input {
  margin: 0.5rem 0;
  padding: 1rem;
  outline: none;
}
input:invalid:not(:placeholder-shown) {
  border: 2px solid red;
}
input:valid:not(:placeholder-shown) {
  border: 2px solid green;
}
.submit {
  border: 2px solid black;
  cursor: pointer;
}
